	var path = 'pages/components/alert - \u526F\u672C/alert.html';
	var html = "<div class=markdown-body><p></p><header id=\"page-header clearfix\"> <h2><i class=\"fa fa-puzzle-piece\"></i>警报信息</h2> <ol class=breadcrumb> <li> <a href=#>首页</a> </li> <li> <a href=#>Web组件</a> </li> <li class=active> <strong>警报信息</strong> </li> </ol> </header><p></p> <h1 id=ui-alert->ui-alert 警报信息</h1> <p class=lead><code>ui-alert</code>指令可以通过静态和动态模型数据(使用 ng-repeat 指令)生成警告信息（alerts）</p> <h2 id=->属性</h2> <ul> <li><p><code>close()</code> <small class=badge>$</small> - <code>alert</code> 关闭触发回调函数，如果此属性存在，<code>alert</code>将显示关闭按钮</p> </li> <li><p><code>dismiss-on-timeout</code> <em>(Default: <code>none</code>)</em> - 指定时间（单位：ms），在指定时间之后，<code>alert</code>自动关闭，此属性需要 <code>close</code> 属性支持。</p> </li> <li><p><code>template-url</code> <em>(Default: <code>../../template/alert/alert.html</code>)</em> - 自定义信息模板url，可以覆盖组件默认的模板</p> </li> </ul> <h2 id=demo>demo</h2> <div class=row> <div class=col-md-5> <div> <script type=text/ng-template id=alert.html> <div ng-transclude></div> </script> <div ui-alert=\"\" ng-repeat=\"alert in alerts\" ng-class=\"'alert-' + (alert.type || 'warning')\" close=closeAlert($index)>{{alert.msg}}</div> <div ui-alert=\"\" template-url=alert.html style=background-color:#fa39c3;color:#fff>A happy alert!</div> <button type=button class=\"btn btn-info\" ng-click=addAlert()>Add Alert</button> </div> </div> <div class=col-md-7> <ui-tabset> <ui-tab> <ui-tab-heading>HTML</ui-tab-heading> <pre><code style=width:100%;color:#fff><span class=xml>\n  <span class=hljs-tag>&lt;<span class=hljs-name>div</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>script</span> <span class=hljs-attr>type</span>=<span class=hljs-string>&quot;text/ng-template&quot;</span> <span class=hljs-attr>id</span>=<span class=hljs-string>&quot;alert.html&quot;</span>&gt;</span><span class=javascript>\n    &lt;div ng-transclude&gt;<span class=xml><span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span></span>\n    </span><span class=hljs-tag>&lt;/<span class=hljs-name>script</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>ui-alert</span>=<span class=hljs-string>&quot;&quot;</span> <span class=hljs-attr>ng-repeat</span>=<span class=hljs-string>&quot;alert in alerts&quot;</span> <span class=hljs-attr>ng-class</span>=<span class=hljs-string>&quot;&#39;alert-&#39; + (alert.type || &#39;warning&#39;)&quot;</span> <span class=hljs-attr>close</span>=<span class=hljs-string>&quot;closeAlert($index)&quot;</span>&gt;</span></span><span class=hljs-template-variable>{{alert.msg}}</span><span class=xml><span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>div</span> <span class=hljs-attr>ui-alert</span>=<span class=hljs-string>&quot;&quot;</span> <span class=hljs-attr>template-url</span>=<span class=hljs-string>&quot;alert.html&quot;</span> <span class=hljs-attr>style</span>=<span class=hljs-string>&quot;background-color:#fa39c3;color:white&quot;</span>&gt;</span>A happy alert!<span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>button</span> <span class=hljs-attr>type</span>=<span class=hljs-string>&quot;button&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;btn btn-default&quot;</span> <span class=hljs-attr>ng-click</span>=<span class=hljs-string>&quot;addAlert()&quot;</span>&gt;</span>Add Alert<span class=hljs-tag>&lt;/<span class=hljs-name>button</span>&gt;</span>\n  <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n</span></code></pre> </ui-tab> <ui-tab> <ui-tab-heading>JavaScript</ui-tab-heading> <pre><code style=width:100%;color:#fff>\nexport <span class=hljs-keyword>default</span> <span class=hljs-class><span class=hljs-keyword>class</span> <span class=hljs-title>AlertCtrl</span> </span>{\n  constructor($scope, $location, $timeout) {\n    <span class=hljs-string>&quot;ngInject&quot;</span>;\n    $scope.alerts = [\n        { type: <span class=hljs-string>&#39;danger&#39;</span>, msg: <span class=hljs-string>&#39;Oh snap! Change a few things up and try submitting again.&#39;</span> },\n        { type: <span class=hljs-string>&#39;success&#39;</span>, msg: <span class=hljs-string>&#39;Well done! You successfully read this important alert message.&#39;</span> }\n      ];\n\n      $scope.addAlert = <span class=hljs-function><span class=hljs-keyword>function</span><span class=hljs-params>()</span> </span>{\n        $scope.alerts.push({msg: <span class=hljs-string>&#39;Another alert!&#39;</span>});\n      };\n\n      $scope.closeAlert = <span class=hljs-function><span class=hljs-keyword>function</span><span class=hljs-params>(index)</span> </span>{\n        $scope.alerts.splice(index, <span class=hljs-number>1</span>);\n      };\n  }\n}\n</code></pre> </ui-tab> </ui-tabset> </div> </div> </div>";
